const { ccclass, property } = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {
  @property(cc.Label)
  label: cc.Label = null;

  @property
  text: string = "hello";
  // 主角跳跃高度
  @property
  jumpHeight: 200;

  //   主角跳跃持续时间
  @property
  jumpDuration: 0.3;

  // 最大移动速度
  @property
  maxMoveSpeed: 400;

  // 加速度
  @property
  accel: 350;

  runJumpAction() {
    // ! 这里涉及到了 缓动（cc.tween）系统，在 Cocos Creator 中，
    // ! cc.tween 可以对任何对象进行操作，并且可以对对象的 任意属性 进行缓动。

    // 上升
    const jumpUp = cc
      .tween()
      .by(this.jumpDuration, { y: this.jumpHeight }, { easing: "sineOut" });
    //  下落
    const jumpDown = cc
      .tween()
      .by(this.jumpDuration, { y: -this.jumpHeight }, { easing: "sineIn" });

    //   创建一个缓动，按jumpUp jumpDown的顺序执行动作
    const tween = cc.tween().sequence(jumpUp, jumpDown);

    // 不断重复
    return cc.tween().repeatForever(tween);
  }

  // LIFE-CYCLE CALLBACKS:

  onLoad() {
    const jumpAction = this.runJumpAction();
    cc.tween(this.node).then(jumpAction).start();
  }

  // start() {}

  // update (dt) {}
}
